<template>
  <Layout class="pushMsgLisg content-data">
    <div class="content-data-card">
      <div
        ref="image"
        class="table-basic">
        <el-table
          :data="tableData.rows"
          style="width: 100%">
          <el-table-column
            prop="date"
            label="日期"/>
          <el-table-column
            prop="userName"
            label="发送人"
            width="90"/>
          <el-table-column
            prop="servicecenterName"
            label="服务中心"/>
          <el-table-column
            prop="account"
            label="接收对象"/>
          <el-table-column
            prop="msg"
            label="消息内容">
            <template slot-scope="scope">
              <div v-if="scope.row.type === '文本'">{{ JSON.parse(scope.row.msg).msg }}</div>
              <div
                v-else
                class="pushMsgLisg-tableImg">
                <img
                  :src="JSON.parse(scope.row.msg).url"
                  alt="">
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="count"
            align="center"
            width="90"
            label="消息总条数"/>
          <el-table-column
            fixed="right"
            label="操作"
            width="90">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="handleClick(scope.row)">发送状态</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div>
        <el-dialog
          :visible.sync="dialogStatusVisible"
          title="发送状态">
          <!-- <div class="pushMsgLisg-selectedInput" >
          <el-select
            v-model="status"
            size="mini"
            placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"/>
          </el-select>
        </div> -->
          <div>

            <el-badge
              v-for="item in detailList.rows"
              :key="item.stuId"
              :value="item.state"
              class="pushMsgLisg-item"
              :type="item.state === '失败' ? 'sucess' : 'danger'">
              {{ item.stuName }}
            </el-badge>

            <div class="pushMsgLisg-detail__pagination">
              <el-pagination
                v-if="detailList.total > detailList.rows.length"
                :page-size="detailPageSize"
                :total="detailList.total"
                :current-page.sync="detailPageNo"
                layout="total, sizes, prev, pager, next, jumper"
                @size-change="handleDetailSizeChange"
                @current-change="pageDetailChangeEv"/>
            </div>

          </div>
        </el-dialog>
      </div>
      <div class="">
        <el-pagination
          v-if="tableData.total > tableData.rows.length"
          :page-size="pageSize"
          :total="tableData.total"
          :current-page.sync="pageNo"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="pageChangeEv"/>
      </div>
    </div>
  </Layout>
</template>
<script>
import 'viewerjs/dist/viewer.css'
import Viewer from 'viewerjs'
import {
  massHistory,
  massDetail
} from '@/api/crm'
export default {
  data () {
    return {
      dialogStatusVisible: false,
      status: '',
      options: [
        {
          value: '',
          label: '全部'
        },
        {
          value: '0',
          label: '成功'
        },
        {
          value: '1',
          label: '失败'
        }
      ],
      tableData: {
        total: 0,
        rows: []
      },
      pageSize: 40,
      pageNo: 1,
      detailPageNo: 1,
      detailPageSize: 50,
      viewer: null,
      detailList: {
        total: 0,
        rows: []
      },
      detailConditon: {}
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.viewer = new Viewer(this.$refs['image'], {
        viewed () {
          this.viewer.zoomTo(0.8)
        },
        navbar: 0,
        toolbar: {
          zoomIn: 4,
          zoomOut: 4,
          oneToOne: 0,
          reset: 4,
          prev: 0,
          play: {
            show: 0,
            size: 'large'
          },
          next: 0,
          rotateLeft: 4,
          rotateRight: 4,
          flipHorizontal: 4,
          flipVertical: 4
        }
      })

      this.gainData()
    })
  },
  methods: {
    pageChangeEv (curPage) { // 列表改变页码
      this.pageNo = curPage
      this.gainData()
    },
    handleSizeChange (curPageSize) { // 列表改变一页展示的条数
      this.pageSize = curPageSize
      this.gainData()
    },
    pageDetailChangeEv (curPage) { // 详情改变页码
      this.detailPageNo = curPage
      this.gainDetailData()
    },
    handleDetailSizeChange (curPageSize) { // 详情改变一页展示的条数
      this.detailPageSize = curPageSize
      this.gainDetailData()
    },
    async gainData () { // 获取列表信息
      let params = {
        page: this.pageNo,
        pageSize: this.pageSize
      }
      massHistory(params).then(data => {
        this.tableData = data
        this.$nextTick(() => {
          this.viewer.update()
        })
      }).catch(err => {
        this.$Message.error({
          content: '获取信息失败：' + err.message,
          duration: 5
        })
      })
    },
    async gainDetailData () { // 获取详情
      let params = {
        page: this.detailPageNo,
        pageSize: this.detailPageSize,
        massId: this.detailConditon.massId
      }
      massDetail(params).then(data => {
        this.detailList = data
      }).catch(err => {
        this.$Message.error({
          content: '获取信息失败：' + err.message,
          duration: 5
        })
      })
    },
    handleClick (val) { // 获取发送状态
      this.dialogStatusVisible = true
      this.detailConditon = val
      this.detailPageNo = 1
      this.gainDetailData()
    }
  }
}
</script>
<style lang="less" scoped>
.pushMsgLisg {
  padding: 20px;
  overflow-x: hidden;
  &-selectedInput {
    margin-top: -25px;
    margin-bottom: 10px;
  }
  &-item {
    width: 70px;
    margin-top: 20px;
    margin-right: 20px;
    text-align: center;
    min-width: 80px;
  }
  &-tableImg {
    width: 100px;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
    }
  }
  &-detail {
    &__pagination {
      margin-top: 20px;
    }
  }
}
</style>
