<template>
  <Layout class="appPushMsg overall-basic">
    <div>
      <el-form
        ref="formStuStatus"
        :model="formStuStatus"
        label-position="top"
        label-width="80px">
        <el-form-item
          :label="filterData.stuStatus.label"
          prop="stuStatus">
          <el-select
            size="small"
            v-model="formStuStatus.stuStatus"
            placeholder="请选择"
            @change="stuStatusChangeEv">
            <el-option
              v-for="item in filterData.stuStatus.data"
              :key="item.stuStatusId"
              :label="item.stuStatusName"
              :value="item.stuStatusId"/>
          </el-select>
        </el-form-item>
      </el-form>
      <el-form
        v-show="formStuStatus.stuStatus === '1'"
        ref="formStuFilter"
        :model="formStuFilter"
        :inline="true"
        :rules="rules"
        label-position="top"
        label-width="80px">
        <el-row :gutter="20">
          <el-col :span="3" style="min-width: 100px">
            <el-form-item
              :label="filterData.stuClassYear.label"
              prop="stuYear">
              <el-select
                size="small"
                v-model="formStuFilter.stuYear"
                filterable
                placeholder="请选择">
                <el-option
                  v-for="item in filterData.stuClassYear.data"
                  :key="item.classYearId"
                  :label="item.classYearName"
                  :value="item.classYearId"/>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="3">
            <el-form-item
              :label="filterData.stuClassTerm.label"
              prop="stuTerm">
              <el-select
                size="small"
                v-model="formStuFilter.stuTerm"
                filterable
                placeholder="请选择">
                <el-option
                  v-for="item in filterData.stuClassTerm.data"
                  :key="item.classTermId"
                  :label="item.classTermName"
                  :value="item.classTermId"/>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="15">
            <el-form-item
              style="width: 100%"
              :label="filterData.stuClassGrade.label"
              prop="stuClassGrade">
              <el-select
                style="width: 100%"
                size="small"
                v-model="formStuFilter.stuClassGrade"
                filterable
                multiple
                clearable
                placeholder="请选择"
                @change="selectGradeHandler">
                <el-option
                  v-for="item in filterData.stuClassGrade.data"
                  :key="item.classGradeId"
                  :label="item.classGradeName"
                  :value="item.classGradeId"/>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="3">
            <el-form-item
              :label="filterData.stuClassSubject.label"
              prop="stuSubject">
              <el-select
                size="small"
                v-model="formStuFilter.stuSubject"
                filterable
                placeholder="请选择">
                <el-option
                  v-for="item in filterData.stuClassSubject.data"
                  :key="item.classSubjectId"
                  :label="item.classSubjectName"
                  :value="item.classSubjectId"/>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <el-form
        v-show="formStuStatus.stuStatus === '2'"
        ref="formStuGrade"
        :model="formStuGrade"
        :rules="rulesGrade"
        label-position="top"
        label-width="80px">
        <el-form-item
          :label="filterData.stuGrade.label"
          prop="stuClassGrade">
          <el-select
            size="small"
            v-model="formStuGrade.stuClassGrade"
            filterable
            placeholder="请选择">
            <el-option
              v-for="item in filterData.stuGrade.data"
              :key="item.stuGradeId"
              :label="item.stuGradeName"
              :value="item.stuGradeId"/>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div>
      <el-tabs
        @tab-click="tabClickEv">
        <el-tab-pane
          name="0"
          label="消息">
          <el-input
            size="small"
            :rows="5"
            :maxlength="300"
            v-model="msgContent"
            class="formItemTextarea"
            type="textarea"
            placeholder="请输入内容"/>
        </el-tab-pane>
        <el-tab-pane
          name="1"
          label="图片">
          <div class="appPushMsg-upload">
            <div class="appPushMsg-upload__container">
              <input
                ref="uploadImg"
                class="appPushMsg-upload__uploadImg"
                type="file"
                @change="uploadImgEv">
              <div
                v-if="!picLoading && !msgImgUrl"
                class="appPushMsg-upload__normal">
                <div class="appPushMsg-upload__placeHolder el-icon-upload"/>
                <div class="appPushMsg-upload__text">点击上传</div>
              </div>
              <div
                v-if="picLoading"
                class="appPushMsg-upload__loading ">
                <div class="appPushMsg-upload__loadingIcon el-icon-loading"/>
                <div class="appPushMsg-upload__loadingtext">正在上传...</div>
              </div>
              <div
                v-if="!picLoading && msgImgUrl"
                class="appPushMsg-upload__sucess">
                <img
                  :src="msgImgUrl"
                  class="appPushMsg-upload__sucessImg"
                  alt="">
                <div
                  class="appPushMsg-upload__del el-icon-circle-close"
                  @click.stop="uploadDelEv"/>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="appPushMsg-btnContainer">
      <el-button
        size="small"
        type="primary"
        @click="sendMsgEv">发送{{Number(tabIndex) === 0 ? '消息': '图片'}}</el-button>
      <el-button
        size="small"
        @click="resetEv">重置</el-button>
    </div>
    <el-dialog
      :visible.sync="dialogVisible"
      title="发送确认"
      width="50%">
      <div class="appPushMsg-dialog">
        <div class="appPushMsg-dialog__item"><span class="appPushMsg-dialog__title">接收对象：</span>{{ confirmMsgData.targetObject }}<span/></div>
        <div class="appPushMsg-dialog__item"><span class="appPushMsg-dialog__title">接收人数：</span>{{ confirmMsgData.targetCount }}<span/></div>
        <div class="appPushMsg-dialog__item"><span class="appPushMsg-dialog__title">消息内容：</span>{{ confirmMsgData.msgContent }}<span/></div>
      </div>
      <span
        slot="footer"
        class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button
          :disabled="submitting"
          type="primary"
          @click="completeSendMsgEv">确认并发送</el-button>
      </span>
    </el-dialog>
  </Layout>
</template>
<script>
import {
  massFilters,
  massConfirm,
  massPeopleCount
} from '@/api/crm'
export default {
  name: 'apppushmsg',
  data () {
    return {
      dialogVisible: false,
      formStuStatus: {
        stuStatus: ''
      },
      formStuFilter: {
        stuYear: '0',
        stuTerm: '0',
        stuClassGrade: [],
        stuSubject: ''
      },
      formStuGrade: {
        stuClassGrade: []
      },
      labelPosition: 'right',
      formLabelAlign: {
        name: '',
        region: '',
        type: ''
      },
      msgContent: '',
      msgImgUrl: '',
      filterData: {
        stuClassYear: {
          label: '在读年份',
          data: [
            // {
            //   classYearId: '0',
            //   classYearName: '不限'
            // },
          ]
        },
        stuStatus: {
          label: '在读状态',
          data: [
            // {
            //   stuStatusId: '0',
            //   stuStatusName: '不限'
            // }
          ]
        },
        stuClassTerm: {
          label: '在读学期',
          data: [
            // {
            //   classTermId: '0',
            //   classTermName: '不限'
            // }
          ]
        },
        stuClassGrade: {
          label: '在读年级',
          data: [
            // {
            //   classGradeId: '13',
            //   classGradeName: '小学组'
            // },
          ]
        },
        stuClassSubject: {
          label: '在读学科',
          data: [
            // {
            //   classSubjectId: '-9223372036854775808',
            //   classSubjectName: '数学'
            // }
          ]
        },
        stuGrade: {
          label: '学员年级',
          data: [
            // {
            //   stuGradeId: '-6',
            //   stuGradeName: '大班'
            // }
          ]
        }
      },
      confirmMsgData: {
        msgContent: '',
        targetCount: '',
        targetObject: ''
      },
      fileData: {},
      tabIndex: 0,
      rules: {
        stuYear: [
          { required: true, message: '请选择在读年份', trigger: 'change' }
        ],
        stuTerm: [
          { required: true, message: '请选择在读学期', trigger: 'change' }
        ],
        stuClassGrade: [
          { required: true, message: '请选择在读年级', trigger: 'change' }
        ],
        stuSubject: [
          { required: true, message: '请选择在读学科', trigger: 'change' }
        ]
      },
      rulesGrade: {
        stuClassGrade: [
          { required: true, message: '请选择在读年份', trigger: 'change' }
        ]
      },
      picLoading: false,
      submitting: false,
      allGrade: [],
      selectAllGrade: false
    }
  },
  async mounted () {
    massFilters().then((data) => {
      this.formStuStatus.stuStatus = '0'
      this.filterData = data
      this.filterData.stuClassGrade.data.forEach(obj => {
        this.allGrade.push(obj.classGradeId)
      })
      console.log(this.allGrade)
    }).catch((err) => {
      this.$Message.warning({ content: '获取年份失败:' + err.message, duration: 5 })
    })
  },
  methods: {
    tabClickEv (val) {
      this.tabIndex = Number(val.name)
    },
    uploadImgEv () {
      this.picLoading = true
      let ipt = this.$refs.uploadImg
      if (!/\.(png|jpg|bmp|jpeg|gif)$/i.test(ipt.value)) {
        this.$message({
          message: '暂不支持发送该类型文件。',
          type: 'warning'
        })
        this.$refs.uploadImg.value = ''
        this.picLoading = false
        return
      }
      if (ipt.value) {
        this.$store.dispatch('imchat/uploadImg', {
          fileInput: ipt
        }).then((file) => {
          this.picLoading = false
          this.msgImgUrl = file.url
          this.fileData = file
        }).catch((e) => {
          this.$message({
            message: `上传失败 ${e}`,
            type: 'warning'
          })
          this.picLoading = false
        })
      }
    },
    async formateParams () {
      let params = {}
      params.stuStatus = this.formStuStatus.stuStatus
      let errMsg = ''
      if (params.stuStatus === '') {
        errMsg = '请选择在读状态'
      }
      if (this.tabIndex === 0) {
        if (!this.msgContent) {
          errMsg = '消息不能为空'
        }
      } else if (this.tabIndex === 1) {
        if (!this.msgImgUrl) {
          errMsg = '图片不能为空'
        }
      }
      if (errMsg) {
        this.$message({
          message: errMsg,
          type: 'error'
        })
        return
      }
      if (params.stuStatus !== '0') {
        let flag = await this.validateEv()
        if (!flag) {
          return false
        }
      }
      params.msg = this.tabIndex === 0 ? this.msgContent : this.fileData.name
      // params.stuClassGradeId = this.formStuFilter.stuClassGrade
      params.stuClassGradeIds = this.formStuFilter.stuClassGrade
      params.stuClassSubjectid = this.formStuFilter.stuSubject
      params.stuClassTerm = this.formStuFilter.stuTerm
      params.stuClassYear = this.formStuFilter.stuYear
      params.stuGradeId = this.formStuGrade.stuClassGrade.length ? this.formStuGrade.stuClassGrade : ''
      return params
    },
    async validateEv () {
      let stuStatus = this.formStuStatus.stuStatus
      let nameStr = ''
      if (stuStatus === '1') {
        nameStr = 'formStuFilter'
      } else if (stuStatus === '2') {
        nameStr = 'formStuGrade'
      }
      const rlt = await this.formPromiseEv(nameStr)
      return rlt
    },
    formPromiseEv (name) {
      return new Promise((resolve, reject) => {
        this.$refs[name].validate(valid => {
          resolve(valid)
        })
      })
    },
    async sendMsgEv () {
      let params = await this.formateParams()
      if (!params) {
        return
      }
      massPeopleCount(params).then((data) => {
        this.confirmMsgData = data
        if (data.targetCount === 0) {
          this.$message({
            message: '没有符合要求的学员',
            type: 'warning'
          })
          return
        }
        this.dialogVisible = true
        this.submitting = false
      }).catch((err) => {
        this.$Message.warning({ content: '发送失败:' + err.message, duration: 5 })
      })
    },
    async completeSendMsgEv () {
      let params = await this.formateParams()
      if (!params) {
        return
      }
      this.submitting = true
      let type = this.tabIndex
      let body = {}
      if (type === 0) {
        body.msg = this.msgContent
      } else if (type === 1) {
        body = this.fileData
      }
      params.type = type
      params.body = JSON.stringify(body)
      massConfirm(params).then((data) => {
        this.dialogVisible = false
        this.$Message.success('发送成功')
        this.$router.push({
          name: 'pushmsglist'
        })
        this.resetEv()
      }).catch((err) => {
        this.submitting = false
        this.$Message.warning({ content: '发送失败:' + err.message, duration: 5 })
      })
    },
    uploadDelEv () {
      this.fileData = {}
      this.msgImgUrl = ''
      this.$refs.uploadImg.value = ''
    },
    stuStatusChangeEv (val) {
      if (val === '0') {
        this.formStuFilter = {
          stuYear: '0',
          stuTerm: '0',
          stuClassGrade: [],
          stuSubject: ''
        }
        this.formStuGrade = {
          stuClassGrade: []
        }
      }
      if (val === '1') {
        this.formStuGrade = {
          stuClassGrade: []
        }
      }
      if (val === '2') {
        this.formStuFilter = {
          stuYear: '0',
          stuTerm: '0',
          stuClassGrade: [],
          stuSubject: ''
        }
      }
    },
    resetEv () {
      let nameStr = ''
      let stuStatus = this.formStuStatus.stuStatus
      if (stuStatus === '1') {
        nameStr = 'formStuFilter'
      } else if (stuStatus === '2') {
        nameStr = 'formStuGrade'
      }
      // this.$refs['formStuStatus'] && this.$refs['formStuStatus'].resetFields()
      this.formStuStatus.stuStatus = '0'
      this.$refs[nameStr] && this.$refs[nameStr].resetFields()
      this.msgContent = ''
      this.uploadDelEv()
    },
    selectGradeHandler (arr) {
      const allVal = this.allGrade[0]
      const allValWithOutAll = this.allGrade.filter(obj => {
        return obj !== allVal
      })
      const lastVal = arr[arr.length - 1]
      const _arrWithOutAll = arr.filter(obj => {
        return obj !== allVal
      })
      // lastVal 选中的最后一个值
      /** 如果 最后一个值为 不限 则选中全部
          如果 最后一个值不为 不限 则 取消不限的选中状态
       */
      if (lastVal !== allVal) {
        // 如果到达边界情况
        if (_arrWithOutAll.length === allValWithOutAll.length) {
          // 如果上次是全选 则此次要取消选中  否则 全选
          if (this.selectAllGrade) {
            this.selectAllGrade = false
            this.formStuFilter.stuClassGrade = []
          } else {
            this.selectAllGrade = true
            this.formStuFilter.stuClassGrade = Object.freeze(this.allGrade)
          }
        } else {
          // 如果没有到边界 则正常选中
          this.selectAllGrade = false
          this.formStuFilter.stuClassGrade = Object.freeze(_arrWithOutAll)
        }
      } else {
        this.selectAllGrade = true
        this.formStuFilter.stuClassGrade = Object.freeze(this.allGrade)
      }
    }
  }
}
</script>
